import React from "react"
import styles from "./skills.module.scss"
import TrimmedContainer from "./trimmed-container";
import CSharp from "../assets/c-sharp.svg"
import JScript from "../assets/javascript.svg"
import TScript from "../assets/typescript-icon.svg"
import HTML5 from "../assets/html-5.svg"
import CSS3 from "../assets/css-3.svg"
import SASS from "../assets/sass.svg"
import SQL from "../assets/SQL-Databases.svg"
import Dart from "../assets/dart.svg"
import Bash from "../assets/bash.svg"
import StencilJS from "../assets/stencil.svg";
import Angular from "../assets/angular-icon.svg";
import ReactLogo from "../assets/reactlogo.svg";
import Webcomponents from "../assets/webcomponents.svg";
import Ionic from "../assets/ionic.svg";
import Jest from "../assets/jest.svg";
import Puppeteer from "../assets/puppeteer.svg";
import Jenkins from "../assets/jenkins.svg";
import Selenium from "../assets/selenium.svg";
import Lambdatest from "../assets/lamddalogo.svg";
import Flutter from "../assets/flutter.svg";
import ASPCore from "../assets/NET_Core_Logo.svg";
import NodeJSLogo from "../assets/Nodejslogo.svg";
import Nginx from "../assets/icon-NGINX-OSS-2.svg";
import GraphQL from "../assets/graphql.svg";
import GatsbyLogo from "../assets/gatsby.svg";
import MariaDB from "../assets/mariadb-icon.svg";
import SQLServer from "../assets/mssql.svg";
import GitIco from "../assets/git-icon.svg";
import Jira from "../assets/Jiralogo.svg";
import PWA from "../assets/pwa.svg";
import RxJS from "../assets/reactivex.svg";

const Skills = () => {


    return (
        <TrimmedContainer>
            <div>
                <h2 className={styles.heading}>My Current Skills</h2>

                <div>
                    <h3>Languages</h3>
                    <p>I started to program back in 2003 and picked up new languages as I progressed throughout my career. <br /> It is amazing to see how these languages developed over the years and just gets better by the day.</p>

                    <div className={styles.langlogos}>       

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="100"
                            data-sal-easing="ease">
                            <JScript />
                            <figcaption>JavaScript</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="200"
                            data-sal-easing="ease">
                            <TScript />
                            <figcaption>TypeScript</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="300"
                            data-sal-easing="ease">
                            <CSharp />
                            <figcaption>C#</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="400"
                            data-sal-easing="ease">
                            <Dart />
                            <figcaption>Dart</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="450"
                            data-sal-easing="ease">
                            <HTML5 style={{ height: `105px` }} />
                            <figcaption>HTML 5</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="500"
                            data-sal-easing="ease">
                            <CSS3 style={{ height: `105px` }} />
                            <figcaption>CSS 3</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="550"
                            data-sal-easing="ease">
                            <SASS />
                            <figcaption>Sass</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="600"
                            data-sal-easing="ease">
                            <SQL />
                            <figcaption>T-SQL</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="650"
                            data-sal-easing="ease">
                            <Bash />
                            <figcaption>Bash</figcaption>
                        </figure>




                    </div>

                    <h3>Frontend</h3>
                    <p>
                        I love all the goodness that frameworks such as Angular, React and Vue brings to the table, yet, I am a strong believer in understanding the fundamentals and patterns that shape these frameworks.
                    <br />
                        Lately, I have started using Web Components and believe they hold a bright future in the frontend.
                    </p>


                    <div className={styles.techlogos}>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="100"
                            data-sal-easing="ease">
                            <PWA style={{ width: `110px` }} />
                            <figcaption>Progressive web apps</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="200"
                            data-sal-easing="ease">
                            <Webcomponents />
                            <figcaption>Web Components</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="300"
                            data-sal-easing="ease">
                            <StencilJS />
                            <figcaption>StencilJS</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="400"
                            data-sal-easing="ease">
                            <RxJS />
                            <figcaption>RxJS</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="450"
                            data-sal-easing="ease">
                            <Angular />
                            <figcaption>Angular</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="500"
                            data-sal-easing="ease">
                            <ReactLogo />
                            <figcaption>React</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="550"
                            data-sal-easing="ease">
                            <GatsbyLogo />
                            <figcaption>Gatsby</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="600"
                            data-sal-easing="ease">
                            <Ionic />
                            <figcaption>Ionic</figcaption>
                        </figure>
                    </div>


                    <p>I have experience using these frontend testing tools.</p>

                    <div className={styles.techlogos}>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="100"
                            data-sal-easing="ease">
                            <Jest />
                            <figcaption>Jest</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="200"
                            data-sal-easing="ease">
                            <Puppeteer />
                            <figcaption>Puppeteer</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="300"
                            data-sal-easing="ease">
                            <Selenium />
                            <figcaption>Selenium</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="400"
                            data-sal-easing="ease">
                            <Lambdatest />
                            <figcaption>Lambdatest</figcaption>
                        </figure>

                    </div>


                    <h3>Backend</h3>
                    <p>
                        I have mainly used the Microsoft stack on Windows and Linux Servers for the backend. I have also been trying my hand at building small microservices using Nodejs.
                    </p>

                    <div className={styles.techlogos}>

                        <figure className={styles.netcorelogo}
                            data-sal="zoom-in"
                            data-sal-delay="100"
                            data-sal-easing="ease">
                            <ASPCore />
                            <figcaption style={{ whiteSpace: `initial` }}>
                                <div style={{ whiteSpace: `nowrap` }}>ASP.NET Core, </div>
                                <div style={{ whiteSpace: `nowrap` }}>EF Core</div>
                            </figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="200"
                            data-sal-easing="ease">
                            <Nginx />
                            <figcaption>NGINX</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="300"
                            data-sal-easing="ease">
                            <NodeJSLogo />
                            <figcaption>NodeJS</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="400"
                            data-sal-easing="ease">
                            <GraphQL />
                            <figcaption>GraphQL</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="500"
                            data-sal-easing="ease">
                            <MariaDB />
                            <figcaption>MariaDB</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="600"
                            data-sal-easing="ease">
                            <SQLServer />
                            <figcaption>MS SQL Server</figcaption>
                        </figure>


                    </div>


                    <h3>Other</h3>
                    <p>
                        I have experience in the following tools.
                    </p>

                    <div className={styles.techlogos}>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="100"
                            data-sal-easing="ease">
                            <GitIco />
                            <figcaption>git</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="200"
                            data-sal-easing="ease">
                            <Jenkins />
                            <figcaption>Jenkins</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="300"
                            data-sal-easing="ease">
                            <Jira />
                            <figcaption>Jira</figcaption>
                        </figure>

                        <figure
                            data-sal="zoom-in"
                            data-sal-delay="600"
                            data-sal-easing="ease">
                            <Flutter />
                            <figcaption>Flutter</figcaption>
                        </figure>

                    </div>

                </div>
            </div>
        </TrimmedContainer >
    );
};

export default Skills;